import React from "react";
import "./Button.css";
//import buildControls from './../../Burger/BuildControls/BuildControls';

const button = (props) =>( 
    
    <button className= {props.buttonType===1 ?  "Button Success" :"Button Danger"}
  
  onClick={props.clicked}
  >
  {props.children}
  
  </button> )
export default button;



