import React from "react";
import BurgerLogo from "../../assests/images/burger-logo.png";
import './Logo.css'


const logo = props => (
  <div className='Logo' style={{height:props.height}}>
    <img alt='logo' src={BurgerLogo} />
  </div>
);

export default logo;



